function refactorDesignFormat(design) {
  const newDesign = {
    ...design,
    components: design.components.map((component) => {
      const newComponent = {
        ...component,
        styles: {
          ...component.styles,
          fontFamily: component.fontFamily,
          fontSize: component.fontSize,
          fontWeight: component.isBold, // ? 'bold' : 'normal',
          textAlign: component.textAlign,
          color: component.fontColor,
          backgroundColor: component.backgroundColor,
          isBold: component.isBold,
          isItalic: component.isItalic,
          isUnderlined: component.isUnderlined,
        },
      }
      // delete newComponent.fontFamily
      // delete newComponent.fontSize
      // delete newComponent.isBold
      // delete newComponent.textAlign
      // delete newComponent.fontColor
      // delete newComponent.backgroundColor
      return newComponent
    }),
  }
  return newDesign
}

